*
{
  margin:0;
  padding:0;
}
body
{
  background-color: lightsteelblue;
}
.app
{
 
  text-align: center;
  color:blue;

}
.app input{
  font-size: 14px;
  border-radius:5px;
  border:1px solid grey;
  outline:none;
  padding:10px 5px;
  width:200px;
}
.submitbtn
{
  background-color: rgb(164, 243, 100);
  color:white;
  border:none;
  outline:none;
  margin-top:10px;
  padding:10px 30px;
  font-size:16px;
  cursor:pointer;

}
.navActive
{
  color:chartreuse;
    font-size: 30px;
    font-weight: bold;

}
nav{
  
  background-color: rgb(239, 23, 228);
  padding:10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width:40%;
  color:white;
  font-weight: bold;


}
nav ul li{
  cursor: pointer;
  font-size:14px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.2);
}

.ans h3{
  color:white;
  background-color:blue ;
}